.App {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  min-height: 100vh;
}

.Container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 10px;
  text-align: center;
}

/* ✅ Imagen flexible y centrada */
img {
  max-height: 300px;
  max-width: 300px;
  width: 80%;
  height: auto;
  object-fit: cover;
}

/* ✅ Botón más proporcionado */
button {
  background-color: crimson;
  color: #fff;
  margin-top: 10px;
  padding: 10px 30px;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  font-size: 1rem;
  transition: transform 0.2s ease, background-color 0.2s ease;
}

button:hover {
  background-color: #b30f2b;
  transform: scale(1.05);
}

.gap-1 {
  gap: 1em;
}

.flex {
  display: flex;
}

.w-full {
  width: 100%;
}

.align-items-center {
  align-items: center;
}

.justify-content-center {
  justify-content: center;
}

.grid {
  display: grid;
}

/* ✅ Tamaño adaptable del temporizador */
.timer {
  font-size: clamp(24px, 6vw, 48px);
  font-weight: bold;
  color: #222;
  text-align: center;
}

/* ✅ Mensaje más pequeño y responsivo */
.timeout-message {
  font-size: clamp(18px, 5vw, 32px);
  color: rgb(0, 0, 0);
  text-align: center;
  white-space: nowrap;
}
